import {setupModal} from '../utils/modal';

const modals = document.querySelectorAll('.modal');
const modalFeedback = document.querySelector('.modal--feedback');
const modalFeedbackBtns = document.querySelectorAll('[data-modal="feedback"]');
const modalSuccess = document.querySelector('.modal--success');
const modalSuccessBtns = document.querySelectorAll('[data-modal="success"]');
const modalLogin = document.querySelector('.modal--login');
const modalLoginBtns = document.querySelectorAll('[data-modal="login"]');
const modalPhoneCode = document.querySelector('.modal--phone-code');
const modalPhoneCodeBtns = document.querySelectorAll('[data-modal="phone-code"]');

const modalProduct = document.querySelector('.modal--product');
const products = document.querySelectorAll('.main-product__link');


//  setupModal(modal, closeCallback, modalBtns, openCallback, noPrevDefault, preventScrollLock)
//      ,
//          - 
const initModals = () => {
  //    ,      
  window.addEventListener('load', () => {
    if (modals.length) {
      modals.forEach((el) => {
        setTimeout(() => {
          el.classList.remove('modal--preload');
        }, 100);
      });
    }
  });

  if (modalFeedback && modalFeedbackBtns.length) {
    setupModal(modalFeedback, false, modalFeedbackBtns, false, false);
  }
  if (modalSuccess && modalSuccessBtns.length) {
    setupModal(modalSuccess, false, modalSuccessBtns);
  }
  if (modalLogin && modalLoginBtns.length) {
    setupModal(modalLogin, false, modalLoginBtns);
  }
  if (modalPhoneCode && modalPhoneCodeBtns.length) {
    setupModal(modalPhoneCode, false, modalPhoneCodeBtns);
  }
  if (products.length) {
    setupModal(modalProduct, false, products);
  }
};

export {initModals};
